.Heading {
  font-weight: 500; }

.Inner {
  display: inline-block;
  position: relative; }

.Heading-1 {
  font-size: 2.5em; }

.Heading-2 {
  font-size: 1.5em; }
  .Heading-2 .Inner::after {
    content: '';
    position: absolute;
    bottom: 3px;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 8px;
    background: #FFCA04; }



