.Link {
  color: #53ACFE;
  display: inline-block;
  position: relative;
  cursor: pointer;
  text-decoration: none; }
  .Link:hover::after, .Link.Selected::after {
    content: '';
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 3px;
    background: #FFCA04; }



